*{
  padding: 0;
  margin: 0;
}
.main-container{
  min-height: 100vh;
}
.footer-txt{
  text-align: center;
  height: 5vh;
}

/* Home */
.home {
  display: flex;
  flex-direction: column;
  height: 95vh;
}

.home__header {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
}

.home__header a {
  margin-right: 20px;
  text-decoration: inherit;
  color: rgba(0, 0, 0, 0.87);
  font-size: 15px;
}

.home__header a:hover {
  text-decoration: underline;
}

.home__headerRight {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 13vw;
}

.home__headerRight > .MuiSvgIcon-root {
  margin-right: 20px;
}

.home__body {
  flex: 1;
  display: flex;
  margin-top: 10%;
  flex-direction: column;
}

.home__body > img {
  object-fit: contain;
  height: 100px;
}

.home__inputContainer {
  display: flex;
  flex-direction: column;
}

@media screen and (max-width: 750px) {

.search {
    max-width: 560px;
}
.home__body > img {
  object-fit: contain;
  height: 70px;
}

}





.search__input {
  display: flex;
  align-items: center;
  height: 30px;
  border: 1px solid lightgray;
  padding: 10px 20px;
  border-radius: 999px;
  width: 75vw;
  margin: 0 auto;
  margin-top: 40px;
  max-width: 560px;
}

.search__input > input {
  flex: 1;
  padding: 10px 20px;
  font-size: medium;
  border: none;
}

.search__input > input:focus {
  outline-width: 0;
  outline: none;
}

.search__inputIcon {
  color: gray;
}

.search__buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}

.search__buttons button {
  margin: 5px;
  padding: 7px 15px;
  background-color: #f8f8f8;
  border: 1px solid white;
  text-transform: inherit;
  color: #5f6368;
}

.search__buttons button:hover {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
  background-image: -webkit-linear-gradient(top, #f8f8f8, #f1f1f1);
  background-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
}


@media screen and (max-width: 750px) {
  .search {
    max-width: 560px;
  }
  .search__input > input:focus {
    outline: none;
  }
  .search__input {
    height: 20px;
    padding: 10px;
    width: 75vw;
    margin-top: 10px;
  }

.search__input > input {
  padding: 10px;
}

}

.search__buttonsHidden {
  display: none !important;
  }



  /* Search Page CSS */
 
  .searchPage__header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
  }
  
  .searchPage__logo {
    object-fit: contain;
    height: 50px;
    margin-right: 50px;
    border-radius: 20px;
  }
  
  .searchPage__headerBody > .search > .search__input {
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
  }
  
  .searchPage__options {
    display: flex;
    align-items: center;
    color: grey;
  }
  
  .searchPage__options a,
  .searchPage__optionsRight a {
    text-decoration: none;
    color: gray;
  }
  
  .searchPage__optionsRight,
  .searchPage__optionsLeft {
    display: flex;
  }
  
  .searchPage__optionsRight {
    margin-left: 80px;
  }
  
  .searchPage__resultLink {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: black;
    margin-bottom: 3px;
  }
  
  .searchPage__option {
    display: flex;
    align-items: center;
    margin-right: 20px;
  }
  
  .searchPage__option a {
    margin-left: 5px;
  }
  
  .searchPage__resultImage {
    object-fit: contain;
    height: 20px;
    width: 20px;
    margin-right: 10px;
  }
  
  .searchPage__result {
    margin: 40px 0;
  }
  
  .searchPage__resultCount {
    color: #70757a;
    font-size: 14px;
  }
  
  .searchPage__results {
    max-width: 650px;
    margin-top: 20px;
    margin-left: 240px;
    margin-bottom: 100px;
  }
  
  .searchPage__resultTitle {
    text-decoration: none;
  }
  
  .searchPage__resultTitle:hover {
    text-decoration: underline;
  }
  
  .searchPage__resultTitle > h2 {
    font-weight: 400;
    color: #1a0dab;
  }
  
  .searchPage__resultSnippet {
    margin-top: 10px;
  }
  
  @media screen and (max-width: 750px) {
    .searchPage__options {
      display: none;
    }
    .searchPage__header {
      display: flex;
      position: sticky;
      flex-direction: column;
      top: 0;
      background-color: white;
      z-index: 100;
      /* align-items: flex-start; */
      /* padding: 30px; */
      border-bottom: 1px solid lightgray;
    }
    
    

    .searchPage__results {
      width: 95%;
      margin-top: 30px;
      margin-left: 2.5%;
      margin-right: 2.5%;
      margin-bottom: 10px;

    }
  
    .searchPage__optionsRight {
    display: none;
  }
  
  .searchPage__header {
       /* width:calc(100%-20px); */
       padding: 10px;
       align-items: center;
    
  }
  
  .searchPage__logo {
    display: block;

    height: 50px;

    object-fit: contain;

    margin-right: 0px;
    /* margin: auto; */
    /* text-align: center; */
    /* align-items: center; */
    border-radius: 0px;

  
  }
  
  }